@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #e9d5ff;
}

/* scrollbar thumb */
::-webkit-scrollbar-thumb {
  background-color: #581c87;
  transition: width 2s ease-in-out;
}

@layer base {
  html {
    @apply font-modeg;
  }
  h1 {
    @apply text-4xl leading-[3rem] dark:text-dappnodeDarkText lg:text-5xl lg:leading-[4rem] xl:text-6xl xl:leading-[5rem];
  }

  h2 {
    @apply text-3xl dark:text-dappnodeDarkText lg:text-5xl lg:leading-[4rem];
  }
  h3 {
    @apply font-poppins text-lg font-bold text-dappnodePurple lg:text-2xl;
  }

  p {
    @apply font-poppins text-xl dark:text-dappnodeDarkText lg:text-2xl;
  }
}
